.form-info__title {
  display: block;
  margin: 30px 20px 0;
  width: 85vmin;

  div {
    float: left;
    left: 0;
  }
  button {
    float: right;
    right: 0;
  }
}
.form-info {
  text-align: center;
  font-size: 9pt;
  width: 85vmin;
  margin: 10px 20px 20px;

  #address {
    margin-top: 10px;
    width: 85vmin;
    border-width: 3px;
  }

  div {
    //display: flex;
    //flex-flow: row nowrap;
    margin-top: 20px;

    input[type="radio"] {
      margin-right: 10px;
      float: left;
    }

    input[type="text"] {
      margin-left: 20px;
      //width: 50vmin;
      min-width: 200px;
      float: right;
      right: 0;
      border-width: 3px;

    }
  }

  button {
    margin-top: 20px;
    width: 30vmin;
    height: 30px;
    border-width: 3px;
    background: #61dafb;
  }
}
.acquire-date {
  color: #000000;
  top: 20px;
  margin: 50px;
  font-weight: bold;
  font-size: 2rem;
  text-align: center;
}

