body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main-container {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin); }
  .main-container .App-logo {
    margin: 100px;
    height: 30vmin; }
  .main-container .App-link {
    color: #61dafb;
    background: #ffffff;
    font-weight: bold;
    font-size: 1.5rem;
    border: 0; }
  .main-container .button__plusfriend {
    margin-top: 30px;
    background: #ffffff;
    border: 0; }
    .main-container .button__plusfriend img {
      width: 20vmin; }

.form-info__title {
  display: block;
  margin: 30px 20px 0;
  width: 85vmin; }
  .form-info__title div {
    float: left;
    left: 0; }
  .form-info__title button {
    float: right;
    right: 0; }

.form-info {
  text-align: center;
  font-size: 9pt;
  width: 85vmin;
  margin: 10px 20px 20px; }
  .form-info #address {
    margin-top: 10px;
    width: 85vmin;
    border-width: 3px; }
  .form-info div {
    margin-top: 20px; }
    .form-info div input[type="radio"] {
      margin-right: 10px;
      float: left; }
    .form-info div input[type="text"] {
      margin-left: 20px;
      min-width: 200px;
      float: right;
      right: 0;
      border-width: 3px; }
  .form-info button {
    margin-top: 20px;
    width: 30vmin;
    height: 30px;
    border-width: 3px;
    background: #61dafb; }

.acquire-date {
  color: #000000;
  top: 20px;
  margin: 50px;
  font-weight: bold;
  font-size: 2rem;
  text-align: center; }

